<template>
  <g class="candlestick">
    <slot></slot>
  </g>
</template>

<script>
  import d3 from '../d3'
  import techan from 'techan'
  import mixins from './mixins'

  export default {
    name: 'ChartCandlestick',
    mixins: [mixins],
    props: {
      x: Array,
      y: Array,
      data: Array
    },
    computed: {
      yScale() {
        console.log('computed y')
        return d3.scaleLinear().range(this.y)
          .domain(techan.scale.plot.ohlc(this.$findUp('data'), this.accessor).domain())
      }
    },
    mounted() {
      this.candlestick = techan.plot.candlestick()
      this.accessor = this.candlestick.accessor()
    },
    updated() {
    },
    methods: {
      draw() {
        let svg = this.$findUp('svg')
        let data = this.$findUp('data')
        this.candlestick.xScale(this.$findUp('xScale')).yScale(this.$findUp('yScale'))
        svg.selectAll('g.candlestick', this.$el).datum(data).call(this.candlestick)
      }
    }
  }
</script>

<style lang="less">
  g.candlestick {
    path {
      fill: none;
      stroke-width: 1;
    }

    path.body {
      stroke-width: 0;
    }

    path.up {
      fill: #0a0;
      stroke: #0a0;
    }

    path.down {
      fill: #f00;
      stroke: #f00;
    }
  }
</style>



