<template>
  <svg class="city-line" :width="width" :height="height" :style="svgStyles"></svg>
</template>

<script>
  import * as d3 from 'd3'

  export default {
    name: 'CityLine',

    data() {
      return {
        data: [
          {x: '', y: 100},
          {x: '', y: 200},
          {x: '', y: 280},
          {x: '', y: 100},
          {x: '', y: 240},
          {x: '', y: 210},
          {x: '', y: 100},
          {x: '', y: 220},
          {x: '', y: 410},
          {x: '', y: 100},
          {x: '', y: 220},
          {x: '', y: 210},
          {x: '', y: 100},
          {x: '', y: 220},
          {x: '', y: 210}
        ],
        initWidth: 500,
        initHeight: 340,
        padding: {
          left: 40, top: 20, right: 10, bottom: 20
        }
      }
    },
    computed: {
      width() {
        return this.initWidth - this.padding.left - this.padding.right
      },
      height() {
        return this.initHeight - this.padding.top - this.padding.bottom
      },
      svgStyles() {
        const p = this.padding
        return {
          padding: `${p.top}px ${p.right}px ${p.bottom}px ${p.left}px`
        }
      },
      xScale() {
        return d3.scaleBand()
          .range([0, this.width])
          .domain(this.data.map(d => d.x))
          .padding(0.1)
      },
      xAxis() {
        return d3.axisBottom(this.xScale)
      },
      yScale() {
        return d3.scaleLinear()
          .range([this.height, 0])
          .domain([0, d3.max(this.data.map(d => d.y))])
      },
      _yScale() {
        return this.yScale
          .copy()
          .range([0, this.height])
      },
      yAxis() {
        return d3.axisLeft(this.yScale)
      }
    },
    mounted() {
      const vm = this
      const svg = d3.select(this.$el)

      svg.append('g')
        .attr('class', 'y axis')
        .attr('transform', 'translate(0, 0)')
        .call(vm.yAxis)

      svg.append('g')
        .attr('class', 'x axis')
        .attr('transform', `translate(0, ${this.height})`)
        .call(vm.xAxis)

      svg.append('g')
        .selectAll('rect.city')
        .data(this.data)
        .enter()
        .append('rect')
        .attr('class', 'city')
        .attr('x', d => vm.xScale(d.x))
        .attr('y', d => vm.height - vm._yScale(d.y))
        .attr('width', vm.xScale.bandwidth())
        .attr('height', d => vm._yScale(d.y))
        .attr('fill', 'steelBlue')

      svg.append('g')
        .selectAll('text.count')
        .data(this.data)
        .enter()
        .append('text')
        .attr('class', 'count')
        .attr('x', d => vm.xScale(d.x))
        .attr('y', d => vm.height - vm._yScale(d.y))
        .attr('dx', '1em')
        .attr('dy', vm.xScale.bandwidth() / 2)
        .attr('text-anchor', 'middle')
        .attr('fill', '#fff')
        .text(d => d.y)
    }
  }
</script>

<style lang="less">
  svg.city-line {
    margin: 20px;

    text {
      font-size: 12px;
    }

    .axis--x path {
      display: none;
    }
  }
</style>



