import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import D3Demo from '@/components/D3Demo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },

    {
      path: '/power',
      name: 'D3Demo',
      component: D3Demo
    }
  ]
})



