<template>
  <g class="y axis percent" :transform="transform">
  </g>
</template>

<script>
  import techan from 'techan'
  import YAxis from './YAxis'

  export default {
    name: 'YPercentAxis',
    mixins: [YAxis],
    methods: {
      draw() {
        let svg = this.$findUp('svg')
        let y = this.$findUp('yScale')
        let data = this.$findUp('data')
        let yScale = y.copy()

        yScale.domain(techan.scale.plot.percent(y, data[data.length - 2].close).domain())
        this.yAxis.scale(yScale)
        this.annotationIns && this.annotation.orient === 'right' && this.annotationIns.translate([this.$findUp('viewWidth'), 0])
        svg.selectAll(`g.${Array.prototype.join.call(this.$el.classList, '.')}`, this.$el).call(this.yAxis)
      }
    }
  }
</script>

<style lang="less">
  g.axis {
    path, line {
      fill: none;
      stroke: #a2a2a2;
    }
    text {
      fill: #a2a2a2;
    }
  }
</style>



