export default {
  methods: {
    $findUp(name) {
      const _findUp = this.$parent.$findUp
      return this[name] || (_findUp ? _findUp(name) : null)
    },
    $getContainer() {
      let node = this
      while (node.$parent.$findUp) {
        node = node.$parent
      }

      return node
    }
  }
}



