<template>
  <ul class="menulist">
    <div class="left">
      <router-link tag="li" to="/">
        
      </router-link>
      <router-link tag="li" to="/power">
        
      </router-link>
      <router-link tag="li" to="/offer">
        
      </router-link>
      <router-link tag="li" to="/depth">
        
      </router-link>
      <router-link tag="li" to="/analyze">
        
      </router-link>
      <router-link tag="li" to="/found">
        
      </router-link>
    </div>
    <div class="right">
      <router-link tag="li" to="/market">
        
      </router-link>
      <router-link tag="li" to="/news">
        
      </router-link>
      <router-link tag="li" to="/deal">
        
      </router-link>
      <router-link tag="li" to="/service">
        
      </router-link>
    </div>
  </ul>
</template>

<script>
  export default {
    name: 'Menu'
  }
</script>

<style scoped lang="less">
  ul.menulist {
    display: flex;
    background: #e4e4e4;
    padding-left: 30px;
    justify-content: space-between;
    .left{
      display: flex;
      li {
        list-style: none;
        color: #000;
        font-size: 12px;
        width: 40px;
        height: 20px;
        line-height: 20px;
        text-align: center;
        cursor: pointer;
        box-sizing: border-box;
        &.router-link-active {
          background: #EBEBEB;
          border: 1px solid #ccc;
          border-top:none;
          border-bottom: none;
          color: #FF0000;
        }
      }
    }
    .right{
      width:150px;
      display: flex;
      li{
        list-style: none;
        color: #000;
        background: -webkit-linear-gradient(#f1f0f0,#bdbdbd ); /* Safari 5.1 - 6.0 */
        background: -o-linear-gradient(#f1f0f0,#bdbdbd ); /* Opera 11.1 - 12.0 */
        background: -moz-linear-gradient(#f1f0f0,#bdbdbd ); /* Firefox 3.6 - 15 */
        background: linear-gradient(#f1f0f0,#bdbdbd);
        font-size: 12px;
        width: 30px;
        height: 18px;
        line-height: 18px;
        text-align: center;
        cursor: pointer;
        margin-left:3px;
        border:1px solid #ccc;
      }
    }
  }
</style>



