<template>
  <div>
    <ul class="kline-menu">
      <li v-for="(name, i) of menus"
          @click="select(i)"
          :class="{active: i === current}">{{name}}</li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'KLineMenu',
    data() {
      return {
        menus: ['', 'K'],
        current: 0
      }
    },
    methods: {
      select(index) {
        if (index === this.current) {
          return
        }

        this.current = index
        this.$emit('changeType', index)
      }
    }
  }
</script>

<style scoped lang="less">
  .kline-menu {
    display: flex;
    background: #313335;
    color: #ddd;
    font-size: 12px;
    border: 1px solid #ddd;
    width: auto;

    li {
      list-style: none;
      height: 24px;
      line-height: 24px;
      padding: 0 10px;
      border-right: 1px solid #ddd;
      cursor: pointer;

      &.active {
        color: #fff;
        background: #B02900;
        cursor: default;
      }
    }
  }
</style>



