<template>
  <div class="simple-line">
    <h1>Line Bar</h1>
  </div>
</template>

<script>
  import * as d3 from 'd3'

  export default {
    name: 'D3Demo',
    data() {
      return {
        data: [12, 25, 13, 23, 22, 44, 24, 44],
        dataRange: d3.scaleLinear().domain([0, 100]).range([0, 100]),
        colorRange: d3.scaleLinear().domain([0, 100]).range(['#29B6F6', '#01579B'])
      }
    },
    mounted() {
      this.render('.simple-line', this.data)

      this.handle = setInterval(() => {
        this.data.shift()
        this.data.push(Math.round(Math.random() * 100))
        this.render('.simple-line', this.data)
      }, 2 * 1000)
    },
    destroyed() {
      clearInterval(this.handle)
    },
    methods: {
      render(selector, data) {
        d3.select(selector).selectAll('div.rect').data(data)
          .enter()
          .append('div')
          .attr('class', 'rect')
          .append('span')

        d3.select(selector).selectAll('div.rect').data(data)
          .style('width', d => `${this.dataRange(d)}px`)
          .style('background', d => this.colorRange(d))
          .select('span')
          .text(d => d)

        d3.select(selector).selectAll('div.rect').data(data)
          .exit()
          .remove()
      }
    }
  }
</script>

<style lang="less">
  .simple-line {
    display: inline-block;
    margin: 20px;
    font-family: "helvetica";

    .rect {
      min-height: 30px;
      min-width: 10px;
      background-color: steelblue;
      margin-bottom: 2px;
      font-size: 11px;
      color: #fff;
      text-align: right;
      padding-right: 2px;
    }
  }
</style>



