<template>
  <g class="y axis" :transform="transform">
    <g class="y grids" :transform="_transform"></g>
  </g>
</template>

<script>
  import * as d3 from 'd3'
  import techan from 'techan'
  import mixins from './mixins'

  export default {
    name: 'YAxis',
    mixins: [mixins],
    props: {
      align: String,
      ticks: {
        type: Number,
        default: 10
      },
      format: String,
      annotation: Object
    },
    computed: {
      transform() {
        return this.align === 'right' ? `translate(${this.$findUp('viewWidth')}, 0)` : 'translate(0, 0)'
      },
      _transform() {
        return this.align === 'right' ? `translate(-${this.$findUp('viewWidth')}, 0)` : 'translate(0, 0)'
      }
    },
    mounted() {
      let yAxis

      // 
      if (this.align === 'right') {
        yAxis = d3.axisRight()
      } else {
        yAxis = d3.axisLeft()
      }
      this.ticks && yAxis.ticks(this.ticks)
      this.format && yAxis.tickFormat(d3.format(this.format))
      this.yAxis = yAxis

      // Grids
      // this.yGrids = d3.axisRight()
      //   .ticks(this.ticks)
      //   .tickFormat('')

      // 
      if (this.annotation) {
        let container = this.$getContainer()
        let annotation = this.annotationIns = techan.plot.axisannotation().axis(this.yAxis)
        let {width, orient, format} = this.annotation

        !!width && annotation.width(width)
        !!orient && annotation.orient(orient)
        !!format && annotation.format(d3.format(format))
        // !!translate && annotation.translate(translate)

        container.yAnnotation = container.yAnnotation || []
        container.yAnnotation.push(annotation)
      }
    },
    updated() {
    },
    methods: {
      draw() {
        let svg = this.$findUp('svg')
        this.yAxis.scale(this.$findUp('yScale'))
        // this.yGrids.scale(this.$findUp('yScale'))
        this.annotationIns && this.annotation.orient === 'right' && this.annotationIns.translate([this.$findUp('viewWidth'), 0])
        svg.selectAll(`g.${Array.prototype.join.call(this.$el.classList, '.')}`, this.$el)
          .call(this.yAxis)
        // svg.selectAll(`g.${Array.prototype.join.call(this.$el.classList, '.')} g.y.grids`)
        //   .call(this.yGrids.tickSize(this.$findUp('viewWidth')))
      }
    }
  }
</script>

<style lang="less">
  g.axis {
    path, line {
      fill: none;
      stroke: #a2a2a2;
    }
    text {
      fill: #a2a2a2;
    }
  }
</style>



