<template>
  <g class="indicator ma">
    <slot></slot>
  </g>
</template>

<script>
  import * as d3 from 'd3'
  import techan from 'techan'
  import mixins from './mixins'

  export default {
    name: 'ChartMA',
    mixins: [mixins],
    props: {
      y: Array,
      type: {
        type: String,
        default: 'sma'
      },
      color: {
        type: String,
        default: '#fff'
      },
      count: {
        type: Number,
        default: 1
      }
    },
    computed: {
      yScale() {
        if (!this.y) {
          return null
        }

        return d3.scaleLinear().range(this.y)
          .domain(techan.scale.plot.ohlc(this.$findUp('data'), techan.plot.candlestick().accessor()).domain())
      }
    },
    mounted() {
      if (this.type === 'ema') {
        this.ma = techan.plot.ema()
      } else {
        this.ma = techan.plot.sma()
      }
    },
    methods: {
      draw() {
        let data = this.$findUp('data')
        let svg = this.$findUp('svg')
        console.log('used', this.count)
        this.ma.xScale(this.$findUp('xScale')).yScale(this.$findUp('yScale'))
        svg.selectAll(`g.${Array.prototype.join.call(this.$el.classList, '.')}`, this.$el)
          .datum(techan.indicator[this.type]().period(this.count)(data))
          .attr('stroke', this.color)
          .call(this.ma)
      }
    }
  }
</script>

<style lang="less">
  g.ma {
    path {
      fill: none;
      stroke-width: 1;
    }

    text {
      fill: #a2a2a2;
      color: #a2a2a2;
    }
  }
</style>



