<template>
  <div class="kline">
    <KLineMenu @changeType="onChangeType"/>
    <ChartContainer :width="width" :height="height" :data="data" :x="x" :y="y" :margin="margin">
      <XAxis class="time" align="bottom" format="%Y-%m" :annotation="timeAnnotation" />
      <ChartCandlestick v-if="type === 1" :y="yCandlestick">
        <ChartMA class="sma-5" color="#1f77b4" type="sma" :count="5" />
        <ChartMA class="sma-10" color="#aec7e8" type="sma" :count="10" />
        <ChartMA class="sma-20" color="#ff7f0e" type="sma" :count="20" />
        <YAxis class="axis-candlestick" :annotation="ohlcAnnotation" :ticks="6" />
        <YPercentAxis align="right" :ticks="6" format="+.1%" :annotation="percentAnnotation" />
      </ChartCandlestick>
      <ChartMA v-if="type === 0" class="f-line" color="#fff" type="sma" :count="1" :y="yCandlestick" >
        <YAxis class="axis-candlestick" :annotation="ohlcAnnotation" :ticks="6" />
        <YPercentAxis align="right" :ticks="6" format="+.1%" :annotation="percentAnnotation" />
      </ChartMA>
      <ChartVolume :y="yVolume">
        <YAxis class="axis-volume" :ticks="3" format=",.3s" :annotation="volumeAnnotation" />
      </ChartVolume>
      <Crosshair />
    </ChartContainer>
  </div>
</template>

<script>
  import $ from 'jquery'
  import _ from 'lodash'

  import d3 from '../d3'
  import ChartContainer from './ChartContainer'
  import ChartCandlestick from './ChartCandlestick'
  import ChartVolume from './ChartVolume'
  import YAxis from './YAxis'
  import YPercentAxis from './YPercentAxis'
  import XAxis from './XAxis'
  import Crosshair from './Crosshair'
  import ChartMA from './ChartMA'
  import KLineMenu from './KLineMenu'

  let originData = require('../data.csv')
  const parseDate = d3.timeParse('%d-%b-%y')

  function d(a) {
    return a.date.getTime()
  }

  /**
   * 
   * @param data
   * @param count
   */
  function parseData(data, count) {
    count = parseInt(count)
    let begin = data.length - count
    begin = begin > -1 ? begin : 0

    return data.map(d => {
      return {
        date: parseDate(d.Date),
        open: +d.Open,
        high: +d.High,
        low: +d.Low,
        close: +d.Close,
        volume: +d.Volume
      }
    }).sort((a, b) => d3.ascending(d(a), d(b)))
      .slice(begin, data.length)
  }

  export default {
    name: 'KLine',

    computed: {
      viewWidth() {
        return this.width - this.margin.left - this.margin.right
      },
      viewHeight() {
        return this.height - this.margin.top - this.margin.bottom
      },
      x() {
        return [0, this.viewWidth]
      },
      y() {
        return [this.viewHeight, 0]
      },
      yCandlestick() {
        return [this.viewHeight - 100, 0]
      },
      yVolume() {
        return [this.viewHeight, this.viewHeight - 80]
      },
      timeAnnotation() {
        return {
          width: 80,
          orient: 'bottom',
          format: '%Y-%m-%d',
          translate: [0, this.viewHeight]
        }
      },
      ohlcAnnotation() {
        return {
          orient: 'left',
          format: ',.2f'
        }
      },
      percentAnnotation() {
        return {
          orient: 'right',
          translate: [this.viewWidth, 0]
        }
      },
      volumeAnnotation() {
        return {
          orient: 'left',
          format: ',.3s'
        }
      }
    },
    data() {
      return {
        width: 0,
        height: 0,
        margin: {
          top: 24, left: 60, bottom: 40, right: 60
        },
        data: [],
        type: 0
      }
    },
    components: {
      ChartContainer,
      ChartCandlestick,
      ChartVolume,
      XAxis,
      YAxis,
      YPercentAxis,
      Crosshair,
      ChartMA,
      KLineMenu
    },
    mounted() {
      const vm = this
      const win = $(window)

      $(window).on('resize', _.debounce(onResize, 100))
        .trigger('resize')

      function onResize() {
        const top = 24
        vm.width = win.width() - 300
        vm.height = win.height() / 2 - top

        if (vm.width < 640) {
          vm.width = 640
        }

        if (vm.height < 375) {
          vm.height = 375 - top
        }

        vm.data = parseData(originData, vm.width / 10)
      }
    },

    methods: {
      onChangeType(type) {
        this.type = type
      }
    }
  }
</script>

<style>
  .kline {
    display: flex;
    flex: auto;
    flex-direction: column;
  }
</style>



