<!---->
<template class="TransactionReduction">
  <table>
    <thead>
    <tr>
      <td></td>
      <td></td>
      <td></td>
      <td></td>
      <td></td>
      <td></td>
      <td></td>
    </tr>
    </thead>
    <tbody>
    <tr v-for="a in newData">
      <td>{{a.timestamp}}</td>
      <td>{{a.price}}</td>
      <td>{{a.Lots}}</td>
      <td v-if="a.hasOwnProperty('type1')" :rowspan="a.type1.rowspan" :class="{ active: a.type1.rowspan > 1 }">{{a.type1.number}}</td>
      <td v-if="a.hasOwnProperty('type2')" :rowspan="a.type2.rowspan" :class="{ active: a.type2.rowspan > 1 }">{{a.type2.number}}</td>
      <td v-if="a.hasOwnProperty('type3')" :rowspan="a.type3.rowspan" :class="{ active: a.type3.rowspan > 1 }">{{a.type3.number}}</td>
      <td v-if="a.hasOwnProperty('type4')" :rowspan="a.type4.rowspan" :class="{ active: a.type4.rowspan > 1 }">{{a.type4.number}}</td>
    </tr>
    </tbody>
  </table>
</template>

<script>
  export default {
    name: 'TransactionReduction',

    data() {
      return {}
    },
    props: {
      TransactionReduction: Array
    },
    computed: {
      newData: function () {
        function getNewType(type) {
          let newType = []
          for (let i = 0; i < type.length; i++) {
            let num = type[i].rowspan - 1
            newType.push(type[i])
            for (let j = 0; j < num; j++) {
              newType.push({})
            }
          }
          return newType
        }

        let data = this.TransactionReduction
        let newData = []
        data.forEach((item, index) => {
          item.type1 = getNewType(item.type1)
          item.type2 = getNewType(item.type2)
          item.type3 = getNewType(item.type3)
          item.type4 = getNewType(item.type4)
          let itemNum = item.Lots.length
          let tempData = {}
          for (let i = 0; i < itemNum; i++) {
            tempData.timestamp = i === 0 ? item.timestamp : ''
            tempData.price = i === 0 ? item.price : ''
            tempData.Lots = item.Lots[i]
            if (item.type1[i] === undefined || JSON.stringify(item.type1[i]) === '{}') {
            } else {
              tempData.type1 = item.type1[i]
              console.log(item.type1[i])
            }
            if (item.type2[i] === undefined || JSON.stringify(item.type2[i]) === '{}') {
            } else {
              tempData.type2 = item.type2[i]
            }
            if (item.type3[i] === undefined || JSON.stringify(item.type3[i]) === '{}') {
            } else {
              tempData.type3 = item.type3[i]
              console.log(item.type3[i])
            }
            if (item.type4[i] === undefined || JSON.stringify(item.type4[i]) === '{}') {
            } else {
              tempData.type4 = item.type4[i]
            }
            newData.push(tempData)
            tempData = {}
          }
        })

        return newData
      }
    }
  }
</script>
<style scoped lang='less'>
  * {
    font-size: 12px;
    color: #a7a2a2;
  }

  .TransactionReduction {
  }

  table {
    width: 100%;
    padding-left: 3px;
    padding-right: 3px;
  }

  thead {
    height: 30px;
    line-height: 30px;
    td {
      text-align: center;
    }
  }
  td{
    text-align: center;
  }

  .active {
    border: 1px solid #fff;
    text-align: center;
  }
  .active {

  }
</style>



