<template>
  <g class="volume">
    <slot></slot>
  </g>
</template>

<script>
  import d3 from '../d3'
  import techan from 'techan'
  import mixins from './mixins'

  export default {
    name: 'ChartVolume',
    mixins: [mixins],
    props: {
      x: Array,
      y: Array,
      data: Array
    },
    computed: {
      yScale() {
        return d3.scaleLinear().range(this.y)
          .domain(techan.scale.plot.volume(this.$findUp('data'), this.accessor.v).domain())
      }
    },
    mounted() {
      this.volume = techan.plot.volume().accessor(techan.accessor.ohlc())
      this.accessor = this.volume.accessor()
    },
    updated() {
    },
    methods: {
      draw() {
        let svg = this.$findUp('svg')
        let data = this.$findUp('data')
        this.volume.xScale(this.$findUp('xScale')).yScale(this.$findUp('yScale'))
        svg.selectAll('g.volume', this.$el).datum(data).call(this.volume)
      }
    }
  }
</script>

<style lang="less">
  g.volume {
    path {
      fill: none;
      stroke-width: 1;
    }

    path.volume {
      fill: #ddd;
      opacity: .2;
    }
  }
</style>



