<template>
  <div class="tian-layout">
    <div class="main">
      <div class="top">
        <slot name="main-top"></slot>
      </div>
      <div class="bottom">
        <slot name="main-bottom"></slot>
      </div>
    </div>
    <div class="aside">
      <div class="top">
        <slot name="aside-top"></slot>
      </div>
      <div class="bottom">
        <slot name="aside-bottom"></slot>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'TianLayout'
  }
</script>

<style scoped lang="less">
  .tian-layout {
    display: flex;
    flex: auto;
    min-height: 750px;
    overflow: hidden;
    .main,
    .aside,
    .top,
    .bottom {
      display: flex;
      flex-direction: column;
      overflow: hidden;
    }

    .main {
      min-width: 640px;
      flex: auto;
      .top {
        height: 50%;
        border-bottom: 1px solid #ddd;
      }
      .bottom {
        flex: auto;
        background: #313335;
      }
    }

    .aside {
      width: 300px;
      .top {
        height: 430px;

      }
      .bottom {
        height: 1px;/* top*/
        flex: auto;
      }
      .bottom {
        height: 340px;
        background: #313335;
      }
    }
  }
</style>



