<template>
  <g class="crosshair">

  </g>
</template>

<script>
  import techan from 'techan'
  import mixins from './mixins'

  export default {
    name: 'Crosshair',
    mixins: [mixins],
    mounted() {
      this.crosshair = techan.plot.crosshair()
    },
    methods: {
      draw() {
        let svg = this.$findUp('svg')
        this.crosshair.xScale(this.$findUp('xScale')).yScale(this.$findUp('yScale'))
          .xAnnotation(this.$findUp('xAnnotation') || [])
          .yAnnotation(this.$findUp('yAnnotation') || [])
        svg.selectAll('g.crosshair', this.$el).call(this.crosshair)
      }
    }
  }
</script>

<style lang="less">
  g.crosshair {
    cursor: crosshair;
    text {
      fill: #a7a2a2;
    }
    path.wire {
      stroke: #a7a2a2;
      stroke-dasharray: 1, 1;
    }

    .axisannotation {
      path {
        fill: #222;
      }
    }
  }
</style>



