<template>
  <div class='outer'>
    <div>
      <div class="title stock-name">
        <span @click="changeData">{{info.stockCode}}</span><span @click="stop">{{info.stockName}}</span>
      </div>
      <div class="title">
        <span>{{news.price}}</span><span>{{news.range}}
        </span><span>{{news.percentage}}%</span>
      </div>
    </div>
    <div class="content">
      <ul>
        <li v-for="(d,i) in sellDetails" v-if="i!=9">
          <span>{{chineseNumber[i]}}</span>
          <span class="price">{{d.price}}</span>
          <span class="num">{{d.num}}</span>
        </li>
      </ul>
      <div class="content-right">
        <div class="header statistics">
          <div class="row">
            <div>
              <span class="left"></span>
              <span class="right">{{statistics.committee}}</span>
            </div>
            <div>
              <span class="left"></span>
              <span class="right">{{statistics.commission}}</span>
            </div>
          </div>
          <div class="row">
            <div>
              <span class="left"></span>
              <span class="right">{{statistics.avgSell}}</span>
            </div>
            <div>
              <span class="left"></span>
              <span class="right">{{statistics.totalSell}}</span>
            </div>
          </div>
        </div>
        <div class="one-details">
          <div class="number" v-for="d in sellOneDetails">{{d}}</div>
        </div>
      </div>
    </div>
    <div class="sell-one one">
      <div class="old">
        <span>{{chineseNumber[9]}}</span>
        <span class="price">{{sellDetails[9].price}}</span>
        <span class="num">{{sellDetails[9].num}}</span>
      </div>
      <div class="new"><span class="num">{{sell.num}}</span><span class="bi"></span><span
        class="trend"></span></div>
    </div>
    <div class="buy-one one">
      <div class="old">
        <span>{{chineseNumber[9]}}</span>
        <span class="price">{{buyDetails[0].price}}</span>
        <span class="num">{{buyDetails[0].num}}</span>
      </div>
      <div class="new"><span class="num">{{buy.num}}</span><span class="bi"></span><span
        class="trend"></span></div>

    </div>
    <div class="content">
      <ul>
        <li v-for="(d,i) in buyDetails" v-if="i!=0">
          <span>{{chineseNumber[9-i]}}</span>
          <span class="price">{{d.price}}</span>
          <span class="num">{{d.num}}</span>
        </li>
      </ul>

      <div class="content-right">
        <div class="one-details">
          <div class="number" v-for="d in buyOneDetails">{{d}}</div>
        </div>
        <div class="footer statistics">
          <div class="row">
            <div>
              <span class="left"></span>
              <span class="right">{{statistics.avgBuy}}</span>
            </div>
            <div>
              <span class="left"></span>
              <span class="right">{{statistics.totalBuy}}</span>
            </div>
          </div>
          <div class="row">
            <div>
              <span class="left"></span>
              <span class="right">{{statistics.totalNum}}</span>
            </div>
            <div>
              <span class="left"></span>
              <span class="right">{{statistics.avgNum}}</span></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'TransactionDetails',
    props: {
      info: Object,
      sellDetails: Array,
      buyDetails: Array,
      news: Object,
      statistics: Object,
      sellOneDetails: Array,
      buyOneDetails: Array,
      sell: Object,
      buy: Object
    },
    data() {
      return {
        chineseNumber: ['', '', '', '', '', '', '', '', '', '']
      }
    },
    methods: {
      changeData() {
        this.$emit('changeData')
      },
      stop() {
        this.$emit('stop')
      }
    }
  }
</script>
<style scoped lang='less'>
  * {
    padding: 0;
    margin: 0;
    border: none;
    box-sizing: border-box;
  }

  .outer {
    min-width: 300px;
    min-height: 300px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #313335;
    color: #a7a2a2;
    border: 1px solid #ddd;
    font-size: 12px;
    height: 1px;
    .price,.num,.number,span.right {
      font-weight: bold;
    }
    .title {
      height: 20px;
      width: 50%;
      float: left;
      display: flex;
      justify-content: space-around;
      align-items: center;
    }
    .content{
      flex: 9 1 0%;
      display: flex;
      align-items: stretch;
      ul {
        list-style: none;
        flex: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        padding:0 2px;
        width: 50%;
        li {
          flex-grow: 1;
          display: flex;
          justify-content: space-between;
          align-items: center;
          span{
            flex: 1 1 0%;
          }
          .price {
            text-align: center;
          }
          .num {
            text-align: right;
          }
        }
      }
      .content-right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 50%;
        .statistics{
          height: 32px;
          .row {
            display: flex;
            justify-content: space-between;
            div {
              flex: 1 1 0;
              display: flex;
              .left {
                text-align: right;
                padding-left: 2px;
              }
              .right {
                flex: 1 1 0;
                text-align: right;
                padding-right: 2px;
              }
            }
          }
        }
        .one-details {
          overflow: hidden;
          display: flex;
          flex-wrap: wrap;
          padding-left: 5px;
          .number {
            height: 20px;
            font-size: 13px;
            min-width: 28px;
            display: flex;
            justify-content: center;
          }
        }
      }
    }
    .one{
      flex: 1 1 0%;
      display: flex;
      align-items: stretch;
      .old{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 50%;
        padding: 0 2px;
        span {
          flex: 1 1 0%;
        }
        .price {
          text-align: center;
        }
        .num {
          text-align: right;
        }
      }
      .new{
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 50%;
        span {
          flex: 1 1 0;
        }
        .num {
          padding-right: 2px;
          text-align: right;
        }
      }
    }
  }

  .sell-one{
    border-bottom: 1px dashed #ddd;
  }

  .stock-name,ul,.old {
    border-right: 1px solid #ddd;
  }
  .title,.header,.buy-one {
    border-bottom: 1px solid #ddd;
  }
  .footer,.sell-one {
    border-top: 1px solid #ddd;
  }



</style>



