
<!---->
<template>
  <div id='DeputeTransactionDetails'>
   <table>
     <ul class="header">
       <li></li>
       <li></li>
       <li></li>
       <li></li>
       <li></li>
       <li></li>
       <li></li>
     </ul>
   </table>
    <table>
      <tbody>
      <template v-for="a in DeputeTransactionDetails ">
        <tr v-for="(b,index) in a.list">
          <td>{{index == 0 ? a.timestamp : ''}}</td>
          <td>{{index == 0 ? a.price : ''}}</td>
          <td align="right">{{b.Lots}}</td>
          <td align="right">{{b.ZBS}}</td>
          <td align="center">{{b.type1 == false ? '':''}}</td>
          <td align="center">{{b.type2 == false ? '':''}}</td>
          <td align="center">{{b.type3 == false ? '':''}}</td>
        </tr>
      </template>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    name: 'DeputeTransactionDetails',
    data () {
      return {

      }
    },
    components: {

    },
    props: {
      DeputeTransactionDetails: Array
    }
  }
</script>

<style scoped lang='less'>
  *{
    font-size: 12px;
    color: #a7a2a2;
  }
  #DeputeTransactionDetails{
      max-height: 475px;
      overflow-y:auto;
      table{
         width: 100%;
         position:relative;
         tbody{
         padding-top:25px;
         display:block;
             td{
               text-align: center;
               padding: 0 5px;
             }
             td:nth-last-child(7){
               padding-left: 0;
             }
             td:nth-last-child(6){
                 padding-left: 0;
             }
            td:nth-last-child(4){
              padding-left: 35px;
            }
          }
     }
     .header{
       display: block;
       position:fixed;
       height:30px;
       line-height:30px;
       background: #000;
       z-index: 1;
       top:473px;
       right: 1px;
       width:298px;
       li{
         float: left;
         list-style: none;
         padding: 0 5px;
       }
     }
  }
</style>



