<!-- 'selectStock' id-->

<template>
  <div id="search-bar" class="sbar">
          <div id="sbar-container"  class="sbar-container">
            <div style="padding: 10px; overflow: auto; max-height: 250px" id="search-bar-scroll-box">
              <div class="sbar-result" v-for='(stock, index) in this.search(this.stockData, this.query)'
                   @click="click"
                    v-on:dblclick="doubleClick">
                <p style="display: none;">{{index}}</p>
                <p :id="index | createId" style="user-select: none; " v-bind:class="{'search-select': index === selection, 'search-normal': index !== selection }">
                  {{stock.exchange}}    {{stock.id}}    {{stock.name}}
                </p>
              </div>
            </div>
            <div id="sbar-input-content" style = "position: absolute;bottom: 9px; outline: none; width: 100%" class="sbar-input">
              <input id="sbar-input-field" autofocus onfocus="this.value = this.value;" v-model="query" placeholder="" class="input"
              @keydown.up="selectUp"
              @keydown.down="selectDown"
              @keydown.enter="selected"
              @input="input">
            </div>
          </div>
  </div>
</template>

<script>
    export default {
      name: 'Sbar',
      props: ['stockData'],

      mounted: function() {
        var vm = this
        var box = document.getElementById('search-bar')
        box.style.visibility = 'hidden'

        window.addEventListener('keyup', function(event) {
          var box = document.getElementById('search-bar')
          var inField = document.getElementById('sbar-input-field')
          var c

          if (event.keyCode === 27) {
            vm.selection = 0
            box.style.visibility = 'hidden'
            return
          }
          if (event.target.nodeName === 'INPUT') return

          if (event.keyCode >= 96 && event.keyCode <= 105) {
            c = event.keyCode - 48
          } else { c = event.keyCode }

          if (c > 47 && c < 91) {
            c = String.fromCharCode(c)
            inField.value = c
            box.style.visibility = 'visible'
            inField.focus()
            vm.query = c
            global.vm.$forceUpdate()
          }
        })
      },

      data() {
        return {
          search(dat, q) {
            var res = []
            var i
            q = q.toLowerCase()
            for (i = 0; i < dat.length; i++) {
              if (dat[i].exchange.includes(q) || dat[i].id.includes(q) || dat[i].name.includes(q) ||
              dat[i].py.includes(q) || dat[i].pyi.includes(q)) { res.push(dat[i]) }
            }
            this.searchResult = res
            return res
          },

          inView(el) {
            var box = document.getElementById('search-bar-scroll-box')
            var tEl = el.offsetTop
            var bEl = tEl + el.scrollHeight
            var tSc = box.scrollTop
            var bSc = tSc + box.offsetHeight
            return ((bSc - el.scrollHeight > tEl) && (tSc + el.scrollHeight < bEl))
          },

          query: '',
          selection: 0,
          searchResult: []
        }
      },

      methods: {

        selectUp: function(e) {
          if (this.selection > 0) { this.selection -= 1 }
          var result = document.getElementById('result' + this.selection)
          if (!this.inView(result)) {
            result.scrollIntoView({behavior: 'instant', block: 'start', inline: 'start'})
          }

          //
        },
        selectDown: function(e) {
          if (this.selection < this.searchResult.length - 1) { this.selection += 1 }
          var result = document.getElementById('result' + this.selection)
          if (!this.inView(result)) {
            result.scrollIntoView({behavior: 'instant', block: 'end', inline: 'start'})
          }
          // document.getElementById('result' + this.selection).scrollIntoView({behavior: 'instant', block: 'end', inline: 'start'})
        },
        click: function(e) {
          var i = e.path[1].children['0'].innerHTML
          this.selection = parseInt(i)
          document.getElementById('sbar-input-field').focus()
        },
        doubleClick: function(e) {
          var i = e.path[1].children['0'].innerHTML
          this.selection = parseInt(i)
          this.$emit('selectStock', this.stockData[this.selection].id)
          document.getElementById('search-bar').style.visibility = 'hidden'
        },
        selected: function(e) {
          this.$emit('selectStock', this.stockData[this.selection].id)
          document.getElementById('search-bar').style.visibility = 'hidden'
        },
        input: function(e) {
          this.selection = 0
        }
      },

      filters: {
        select(i) {
          return (!(i === this.selection))
        },
        createId(i) {
          return 'result' + i
        }
      }

    }
</script>

<style scoped lang="less">
  .input{
    text-transform: uppercase;
  }
  .sbar{
    position: fixed;
    z-index: 999998;
    font-size: 14px;
    bottom: 0px;
    right: 1%;
    border-style: groove;
  }

  .sbar-container{
    background-color:#fff;
    width:200px;
    height:300px;
  }
  .sbar-input{
    position: absolute;
    bottom: 30px;
    left: 17px;
  }
  .sbar-result{
    left: 17px;
    top: 20px;
    color: #000;
    font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
    font-weight: 400;
  }
  .search-select{
    font-weight: bold;
    border-style: groove;
    padding: 1px 5px 2px 10px;
  }
  .search-normal{
    padding: 2px 5px 2px 13px;
  }
</style>



