<template>
  <div class="tabel-change">
    <ul class="top">
      <li v-for="(d,i) in tabelArray" @click="exChange(i)" :class="selectIndex===i ? 'active':''">{{d}}</li>
    </ul>
    <div class="content">
      <DeputeTransactionDetails v-show="selectIndex===0"
                                :DeputeTransactionDetails="DeputeTransactionDetails"></DeputeTransactionDetails>
      <TransactionReduction v-show="selectIndex===1"
                            :TransactionReduction="TransactionReduction"></TransactionReduction>
      <EntrustingaDeal v-show="selectIndex===2" :EntrustingaDeal="EntrustingaDeal"></EntrustingaDeal>
    </div>
  </div>
</template>

<script>
  import EntrustingaDeal from './EntrustingaDeal'
  import DeputeTransactionDetails from './DeputeTransactionDetails'
  import TransactionReduction from './TransactionReduction'

  export default {
    name: 'TabelChange',

    data() {
      return {
        selectIndex: 0,
        tabelArray: ['', '', '']
      }
    },

    props: {
      EntrustingaDeal: Array,
      DeputeTransactionDetails: Array,
      TransactionReduction: Array
    },

    components: {
      EntrustingaDeal,
      DeputeTransactionDetails,
      TransactionReduction
    },

    methods: {
      exChange(index) {
        this.selectIndex = index
      }
    }
  }
</script>

<style scoped lang="less">
  .tabel-change {
    height: 100%;
    background: #313335;
    border: 1px solid #ddd;
    border-top: none;
  .top {
    display: flex;
    background: #191919;
    border-bottom: 1px solid #ddd;
  li {
    list-style: none;
    color: #999;
    font-size: 12px;
    flex: 1 0 0%;
    height: 22px;
    line-height: 22px;
    text-align: center;
    border-right: 1px solid #ddd;
    cursor: pointer;
  &:hover {
     color: #fff;
     background: #494949;
   }
  &.active {
     background: #B02900;
     color: #fff;
   }
  }
  }
  .content {
    color: #fff;
  }
  }
  * ::-webkit-scrollbar{
    width: 0;
    height: 0;
  }
</style>



