<template>
  <svg
    class="container"
    :width="width"
    :height="height"
  >
    <g :transform="transform">
      <slot></slot>
    </g>
  </svg>
</template>

<script>
  import * as d3 from 'd3'
  import techan from 'techan'

  export default {
    name: 'ChartContainer',
    data() {
      return {
        svg: null
      }
    },
    props: {
      width: Number,
      height: Number,
      data: Array,
      x: Array,
      y: Array,
      margin: Object
    },
    computed: {
      transform() {
        return `translate(${this.margin.left}, ${this.margin.top})`
      },
      viewWidth() {
        return this.width - this.margin.left - this.margin.right
      },
      viewHeight() {
        return this.height - this.margin.top - this.margin.bottom
      },
      xScale() {
        console.log('computed x root')
        return techan.scale.financetime().range(this.x)
          .domain(this.data.map(d => d.date))
      },
      yScale() {
        console.log('computed y root')
        return d3.scaleLinear().range(this.y)
      }
    },
    mounted() {
      this.svg = d3.select('svg')
    },
    updated() {
      this.redraw()
    },
    methods: {
      $findUp(name) {
        return this[name]
      },

      redraw() {
        _walk(this, n => (n.draw && n.draw()))

        function _walk(node, cb) {
          node.$children.forEach(n => {
            cb(n)
            _walk(n, cb)
          })
        }
      }
    }
  }
</script>

<style>
  .container {
    display: flex;
    justify-content: center;
    align-items: center;

    background: #313335;
    font: 12px sans-serif;
  }
</style>



