<template>
  <g class="x axis" :transform="transform">
    <g class="x grids" :transform="_transform"></g>
  </g>
</template>

<script>
  import * as d3 from 'd3'
  import techan from 'techan'
  import mixins from './mixins'

  export default {
    name: 'XAxis',
    mixins: [mixins],
    props: {
      align: {
        type: String,
        default: 'left'
      },
      ticks: {
        type: Number,
        default: 5
      },
      format: {
        type: String,
        default: '%Y-%m'
      },
      annotation: Object
    },
    computed: {
      transform() {
        return `translate(0, ${this.$findUp('viewHeight')})`
      },
      _transform() {
        return `translate(0, -${this.$findUp('viewHeight')})`
      }
    },
    mounted() {
      this.xAxis = d3.axisBottom()
        .ticks(this.ticks)
        .tickFormat(d3.timeFormat(this.format))

      this.xGrids = d3.axisBottom(this.$findUp('xScale'))
        .ticks(this.ticks)
        .tickSize(this.$findUp('viewHeight'))
        .tickFormat('')

      if (this.annotation) {
        let container = this.$getContainer()
        let annotation = this.annotationIns = techan.plot.axisannotation().axis(this.xAxis)
        let {width, orient, format} = this.annotation

        !!width && annotation.width(width)
        !!orient && annotation.orient(orient)
        !!format && annotation.format(d3.timeFormat(format))

        container.xAnnotation = container.xAnnotation || []
        container.xAnnotation.push(annotation)
      }
    },
    updated() {
    },
    methods: {
      draw() {
        let svg = this.$findUp('svg')
        this.xAxis.scale(this.$findUp('xScale'))
        this.xGrids.scale(this.$findUp('xScale'))
        this.annotationIns && this.annotationIns.translate([0, this.$findUp('viewHeight')])
        svg.selectAll('g.x.axis', this.$el).call(this.xAxis)
        svg.selectAll('g.x.grids').call(this.xGrids.tickSize(this.$findUp('viewHeight')))
      }
    }
  }
</script>

<style lang="less">
  g.axis {
    path, line {
      fill: none;
      stroke: #a2a2a2;
    }
    text {
      fill: #a2a2a2;
    }

    .grids {
      path, line {
        opacity: .2
      }
    }
  }
</style>



